import { defineComponent, ref, watch } from "vue";
export default defineComponent({
  props: {
    modelValue: {
      type: Object,
      require: true
    },
    formData: {
      type: Object,
      require: true
    },
    formItems: {
      type: Array,
      default: () => []
    },
    labelWidth: {
      type: String,
      default: "100px"
    },
    itemStyle: {
      type: Object,
      default: () => ({
        padding: "10px 40px"
      })
    },
    colLayout: {
      type: Object,
      default: () => ({
        xl: 6,
        lg: 8,
        md: 12,
        sm: 24,
        xs: 24
      })
    }
  },
  emits: ["update:modelValue"],

  setup(props, {
    emit
  }) {
    const formData = ref({ ...props.modelValue
    });
    watch(formData, newValue => {
      emit("update:modelValue", newValue);
    }, {
      deep: true
    });
    return {
      formData
    };
  }

});